import getSources from "../background/sources/getSources";
import { getInstSources } from "./instSourceList";
import { getLocalStorageKeyData } from "../lib/storage";

import { currentBrowser } from "../lib/constants";

//Google Scholar constants
const GSCHOLAR_SETTINGS_HOST = "https://scholar.google";
const GSCHOLAR_SETTINGS_PATH = "scholar_settings";
const GSCHOLAR_STORAGE_KEY = "gScholar";

// Activate selected sources' Libraries on Google Scholar
export const activateSourceLibOnGS = () => {
  const chosenSources = getInstSources();
  const { gScholarSourceList = [] } = getSources();
  const gScholarInstIdList = chosenSources
    .map((chosenSource) =>
      gScholarSourceList.find((gsSource) => gsSource.id === chosenSource.id)
    )
    .filter((gsSource) => gsSource?.gScholar?.instId)
    .map((gsSource) => gsSource.gScholar.instId);

  if (gScholarInstIdList && gScholarInstIdList.length > 0) {
    const uiLanguages = ["fr", "com"];
    for (const uiLanguage of uiLanguages) {
      const gsUrl = `${GSCHOLAR_SETTINGS_HOST}.${uiLanguage}`;
      // Listener to detect when the GS tab has finished loading
      const gsTabListener = (details) => {
        if (details?.url && details.tabId) {
          if (details.url.startsWith(`${gsUrl}/${GSCHOLAR_SETTINGS_PATH}?`)) {
            currentBrowser.tabs.executeScript(details.tabId, {
              code: `document.getElementsByName("save")[0].click();`,
            });
          } else if (details.url.startsWith(`${gsUrl}/scholar?`)) {
            currentBrowser.webNavigation.onCompleted.removeListener(
              gsTabListener
            );
            currentBrowser.tabs.remove(details.tabId);
          }
        }
      };
      currentBrowser.webNavigation.onCompleted.addListener(gsTabListener); // Add GS tab listener
      currentBrowser.tabs.create({
        url: `${gsUrl}/${GSCHOLAR_SETTINGS_PATH}?inst=${gScholarInstIdList.join(
          "&inst="
        )}&save=#2`,
        active: false,
      }); // Open GS tab according to browser language
    }
  }
};

export const hasGScholarSource = () => {
  const { gScholarSourceList = [] } = getSources();
  if (gScholarSourceList?.length > 0)
    return getInstSources().some((instSource) =>
      gScholarSourceList.some((gsSource) => gsSource.id === instSource.id)
    );
};

export const readGScholarActive = async () => {
  const { gScholar } = await getLocalStorageKeyData(GSCHOLAR_STORAGE_KEY);
  return gScholar;
};

export const saveGScholarActive = async (gsEnabled) => {
  await currentBrowser.storage.local.set({
    [GSCHOLAR_STORAGE_KEY]: gsEnabled,
  });
};
