import { mapLimit } from "async";

import fetchApi from "../../lib/fetchApi";
import sendLogsToServer from "../../lib/sendLogsToServer";
import { buildGetOpenUrlParams } from "./openUrl";
import { getCacheData, setCacheData } from "../../lib/storage";

import { PII_TYPE, SID_CLICKANDREAD } from "../../lib/constants";

export default class {
  constructor(sourceName, ftfAccount, ignoredLinkNames) {
    this.ftfAccount = ftfAccount;
    this.apiOpenUrl = `https://api.ebsco.io/ftf/ftfaccount/${this.ftfAccount}.main.ftf/openurl`;
    this.rateLimitRemaining = 10000;
    this.linkIqReqCount = 0;
    this.linkIqReqLimit = 200;
    this.sourceName = sourceName;
    this.ignoredLinkNames = ignoredLinkNames;
    this.cacheMaxAge = 60000 * 45; // 60000 seconds = 1 minute * 45 minutes
  }

  /**
   * Resolve identifiers on Ebsco-LinkIQ API
   * @param  {array} idList Array of resource identifiers {id, node, type}
   * @returns {array} Resolved Id List {id, url, source}
   */
  async resolveIds({ idList = [] }) {
    let resolvedIds = [];
    if (idList.length === 0 || !this.ftfAccount) return resolvedIds;
    resolvedIds = await mapLimit(idList, 50, async (identifier) => {
      if (
        identifier.type !== PII_TYPE &&
        this.linkIqReqCount <= this.linkIqReqLimit &&
        this.rateLimitRemaining > 0
      ) {
        const apiOpenUrl = new URL(this.apiOpenUrl);
        let resolvedIdData;
        // Resolve ID on cache storage
        apiOpenUrl.search = "";
        apiOpenUrl.searchParams.set("sid", `${SID_CLICKANDREAD}`);
        buildGetOpenUrlParams(identifier).forEach((val, key) =>
          apiOpenUrl.searchParams.set(key, val)
        );
        const cacheData = await getCacheData({
          url: apiOpenUrl,
          maxAge: this.cacheMaxAge,
        });
        resolvedIdData = await cacheData?.json();
        if (!resolvedIdData) {
          // Resolve ID on API
          this.linkIqReqCount++;
          const { response, error } = await fetchApi({
            url: apiOpenUrl,
          });
          if (response?.ok) {
            await setCacheData({
              url: apiOpenUrl,
              response: response.clone(),
            }); // Add API response to cache
            // rate-limit headers
            response?.headers?.forEach((val, key) => {
              if (key.startsWith("x-ratelimit-remaining"))
                this.rateLimitRemaining = val;
            });
            resolvedIdData = await response.json();
          } else if (error) {
            sendLogsToServer({
              source: this.sourceName,
              type: identifier.type,
              ...error,
            });
            return error;
          }
        }
        if (resolvedIdData) {
          // First valid targetLink is the best
          const targetLink =
            resolvedIdData?.contextObjects[0]?.targetLinks?.find(
              (targetLink) =>
                // Avoid ignored target link name
                targetLink.targetUrl &&
                targetLink.linkName &&
                !this.ignoredLinkNames.includes(targetLink.linkName.trim())
            );
          if (targetLink)
            return {
              id: identifier.id,
              url: targetLink.targetUrl,
              source: this.sourceName,
            };
        }
      }
    });
    resolvedIds = resolvedIds.filter(
      (identifier) => identifier?.id && identifier?.url
    );
    return resolvedIds;
  }
}
