import { currentBrowser } from "../lib/constants";

const CACHE_NAME = "clickandread";

/**
 * Reads all data out of storage local and exposes it via a promise.
 * Note: Once the Storage API gains promise support, this function can be greatly simplified.
 * @param {String, Array} keys Key values to be red from local storage
 * @returns Saved value
 */
export function getLocalStorageKeyData(keys) {
  // Immediately return a promise and start asynchronous work
  return new Promise((resolve, reject) => {
    // Asynchronously fetch all data from storage local.
    currentBrowser.storage.local.get(keys, (val) => {
      // Pass any observed errors down the promise chain.
      if (currentBrowser.runtime.lastError) {
        console.error(
          "Storage local Runtime Error",
          currentBrowser.runtime.lastError
        );
        return reject(currentBrowser.runtime.lastError);
      }
      // Pass the data retrieved from storage down the promise chain.
      resolve(val);
    });
  });
}

export const getCacheData = async ({ url, maxAge = 0 }) => {
  try {
    const cache = await window.caches?.open(CACHE_NAME);
    const cacheData = await cache?.match(url); // Match url on cache
    // Validate cache freshness
    if (cacheData) {
      const dateHeader = cacheData?.headers?.get("date");
      if (dateHeader) {
        const cacheTime = new Date(dateHeader).getTime();
        if (cacheTime + maxAge > Date.now()) return cacheData;
      }
    }
  } catch (error) {
    console.error(error);
  }
};

export const setCacheData = async ({ url, response }) => {
  try {
    // Check if response has date accessible header
    let cacheResponse = response;
    if (!response?.headers?.get("date")) {
      const headers = new Headers(response?.headers);
      headers.append("date", new Date().toISOString());
      cacheResponse = new Response(response?.body, {
        status: response?.statusCode,
        statusText: response?.statusText,
        headers,
      });
    }
    const cache = await window.caches?.open(CACHE_NAME);
    await cache?.put(url, cacheResponse); // Update cache with fresh API response
  } catch (error) {
    console.error(error);
    if (
      error instanceof DOMException &&
      (error.name === "QuotaExceededError" || // everything except Firefox
        error.name === "NS_ERROR_DOM_QUOTA_REACHED") // Firefox
    )
      await window.caches?.delete(CACHE_NAME); // Delete C&R cache
  }
};
