import chunk from "lodash.chunk";
import { mapSeries } from "async";

import getApiList from "./api/getApiList";
import {
  registerServiceWorker,
  subscribeToWebPush,
  getPushSubscription,
  getSWRegistration,
} from "./notification/webPush";
import { currentBrowser } from "../lib/constants";
import { readDataCollect } from "../options/dataCollect";

const CHUNK_ARRAY_SIZE = 50;

// Listener on addon install to open options page
currentBrowser.runtime.onInstalled.addListener(async ({ reason }) => {
  if (reason === "install") {
    currentBrowser.runtime.openOptionsPage();
    if ("serviceWorker" in navigator) {
      await registerServiceWorker();
      await subscribeToWebPush();
    } // Subscribe to C&R server notifications
  } else if (reason === "update") {
    const dataCollect = await readDataCollect();
    if (dataCollect === undefined) currentBrowser.runtime.openOptionsPage();
    if ("serviceWorker" in navigator) {
      const { version } = currentBrowser.runtime.getManifest();
      const title = `${currentBrowser.i18n.getMessage("notificationTitle")}`;
      const body = `${currentBrowser.i18n.getMessage(
        "notificationBody"
      )}: ${version}`;
      const icon = "../assets/img/icon_1024x1024.png";
      await navigator?.serviceWorker
        .getRegistration()
        .then((registration) => {
          registration?.showNotification(title, {
            body,
            icon,
          }); // Show a notification with title and message
        })
        .catch((error) => {
          console.error(error);
        });
      if (!(await getSWRegistration())) await registerServiceWorker();
      if (!(await getPushSubscription())) await subscribeToWebPush();
    }
  }
});

// Listener on extension's icon click to open options page
currentBrowser.browserAction.onClicked.addListener((tab) =>
  currentBrowser.runtime.openOptionsPage()
);

// Listener on first inserted C&R button in page, to set browser extension icon
currentBrowser.runtime.onMessage.addListener(
  ({ btnInserted }, sender, sendResponse) => {
    if (btnInserted) {
      currentBrowser.browserAction.setIcon({
        path: "../assets/img/badge32.png",
        tabId: sender.tab.id,
      });
    }
  }
);

// Listener on sent parsed ID list from content script to be resolved
currentBrowser.runtime.onConnect.addListener((port) => {
  port?.onMessage.addListener(async ({ parsedIdList }) => {
    if (parsedIdList?.length > 0) {
      // Chunk parsed Id list into arrays
      for await (const idList of chunk(parsedIdList, CHUNK_ARRAY_SIZE)) {
        // Iterate on sources' API (in preset order) to resolve Id list
        let searchedIdList = idList;
        try {
          await mapSeries(await getApiList(), async (api) => {
            if (searchedIdList.length > 0) {
              const resolvedIdList = await api.resolveIds({
                idList: searchedIdList,
              }); // Resolve parsed IDs on each sources' API
              if (resolvedIdList) {
                // Send message with resolved ID list to insert their respective C&R button
                port?.postMessage({
                  resolvedIdList,
                });
                // Filter non resolved IDs for next API call
                searchedIdList = searchedIdList.filter(
                  (searchedId) =>
                    !resolvedIdList.find(
                      (resolvedId) =>
                        searchedId.id.toLowerCase() ===
                        resolvedId.id.toLowerCase()
                    )
                );
              }
            }
          });
        } catch (error) {
          console.error(error);
        }
      }
    }
  });
});
