import $ from "jquery";
import "select2/dist/js/select2.full";
import "select2/src/js/select2/i18n/fr";
import "select2/src/js/select2/i18n/en";
import "bootstrap/dist/js/bootstrap";

import getSources from "../background/sources/getSources";
import {
  getInstSourceItems,
  getInstSources,
  updateInstSources,
} from "./instSourceList";
import { openBibDomainModal } from "./bibDomains";
import { getLocalStorageKeyData } from "../lib/storage";
import institutions from "../data/institutions.json" assert { type: "json" };

import { BIBCNRS_SOURCE_PREFIX, currentBrowser } from "../lib/constants";

export const instSelect = $("#instList"); // init institution select list
const INST_STORAGE_KEY = "institutions"; // Institutions' storage key;
const CNRS_INST_PREFIX = "CNRS";

export const initSelectInst = () => {
  instSelect.select2({
    sorter: (data) =>
      data.sort((inst1, inst2) =>
        inst1.text.localeCompare(inst2.text, "fr", {
          ignorePunctuation: true,
        })
      ),
    data:
      institutions.map((inst) => {
        inst.id = inst.text;
        return inst;
      }) ?? [],
    language: currentBrowser.i18n.getUILanguage(),
    placeholder: currentBrowser.i18n.getMessage("selectInstPlaceholder"),
    width: "100%",
    theme: "bootstrap-5",
  }); // Init institution select list
  // institution list select event listener
  instSelect.on("select2:select", (e) => {
    updateInstitutions([e.params.data]); // Update the selected institution list
    const { sourceList, panistSource } = getSources();
    const { panistId, sourceId, text } = e.params.data;
    const currInstSources = getInstSources();
    const addedInstSources = [];
    document.getElementById("selectInstWarning").hidden = true;
    if (panistId) addedInstSources.push(panistSource);
    if (sourceId) {
      const source = sourceList.find((source) => source.id === sourceId);
      addedInstSources.push(source);
    }
    updateInstSources(
      currInstSources.concat(
        addedInstSources.filter(
          (addedInstSource) =>
            !currInstSources.some(
              (currInstSource) => currInstSource.id === addedInstSource.id
            )
        )
      )
    );
    if (text.includes(CNRS_INST_PREFIX)) openBibDomainModal(); // Open BibDomains Modal and update the source list with the selected BibDomains on closing.
  });
};

export const getInstitutions = () =>
  Array.from(document.querySelectorAll("#instGroup .list-group-item")).map(
    (inst) => ({
      text: inst.textContent,
      panistId: inst.dataset?.panistId,
      sourceId: inst.dataset?.sourceId,
    })
  ); // Get selected institutions

export const updateInstitutions = (instList = []) => {
  const updatedInstList = instList
    .filter(
      (inst) =>
        !getInstitutions().some((currInst) => inst.text === currInst.text)
    )
    .map(({ panistId, sourceId, text } = {}) => {
      const instEl = document.createElement("div");
      instEl.classList.add("list-group-item");
      instEl.textContent = text;
      if (sourceId) instEl.dataset.sourceId = sourceId;
      if (panistId) instEl.dataset.panistId = panistId;
      const removeInstBtn = document.createElement("button");
      removeInstBtn.type = "button";
      removeInstBtn.classList.add("btn-close", "me-3");
      removeInstBtn.ariaLabel = "Close";
      removeInstBtn.addEventListener("click", (e) => {
        const removedInstElem = e.target.parentElement;
        const { sourceId, panistId } = removedInstElem.dataset;
        const text = removedInstElem.textContent;
        removedInstElem.remove();
        const { panistSource } = getSources();
        const instSourceItems = getInstSourceItems();
        if (sourceId)
          instSourceItems
            ?.find((sourceItem) => sourceItem?.dataset?.sourceId === sourceId)
            ?.remove(); // remove the source element corresponding to the removed intstitution
        if (panistId && !getInstitutions().some((inst) => inst?.panistId))
          instSourceItems
            ?.find(
              (sourceItem) => sourceItem?.dataset?.sourceId === panistSource.id
            )
            ?.remove(); // remove the panist source corresponding to the removed intstitution
        if (text.startsWith(CNRS_INST_PREFIX))
          instSourceItems
            ?.filter((instSourceItem) =>
              instSourceItem?.dataset?.sourceId.startsWith(
                BIBCNRS_SOURCE_PREFIX
              )
            )
            .map((bibSourceItem) => bibSourceItem?.remove()); // remove the BibCnrs sources corresponding to the removed intstitution
        if (getInstitutions().length === 0) {
          document.getElementById("sourceList").replaceChildren();
          updateInstSources();
          document.getElementById("source-container").hidden = true;
          document.getElementById("gs-container").hidden = true;
        }
      });
      instEl.insertAdjacentElement("afterbegin", removeInstBtn);
      return instEl;
    });
  document.getElementById("instGroup").append(...updatedInstList);
  instSelect.val(null).trigger("change"); // Reinit select on default placeholder
};

export const readInstitutions = async () => {
  const { [INST_STORAGE_KEY]: savedInstitutions } =
    await getLocalStorageKeyData([INST_STORAGE_KEY]); // Get saved options' settings from local storage
  return savedInstitutions;
};

export const saveInstitutions = async () => {
  await currentBrowser.storage.local.set({
    [INST_STORAGE_KEY]: getInstitutions(),
  });
};
