import { DOI_TYPE, PMID_TYPE, PII_TYPE, COINS_TYPE } from "../../lib/constants";

export const buildGetOpenUrlParams = (identifier) => {
  if (identifier.type === DOI_TYPE)
    return new URLSearchParams(`rft_id=info:doi/${identifier.id}`);
  else if (identifier.type === PMID_TYPE)
    return new URLSearchParams(`rft_id=info:pmid/${identifier.id}`);
  else if (identifier.type === PII_TYPE)
    return new URLSearchParams(`rft_id=info:pii/${identifier.id}`);
  else if (identifier.type === COINS_TYPE)
    return new URLSearchParams(identifier.id);
};

export const buildPostOpenUrlParams = (identifier) => {
  if (identifier.type === DOI_TYPE)
    return {
      rft_id: `info:doi/${identifier.id}`,
    };
  else if (identifier.type === PMID_TYPE)
    return {
      rft_id: `info:pmid/${identifier.id}`,
    };
  else if (identifier.type === PII_TYPE)
    return {
      rft_id: `info:pii/${identifier.id}`,
    };
  else if (identifier.type === COINS_TYPE) {
    // Build params object from OpenUrl
    const openUrlParams = new URLSearchParams(identifier.id);
    return Object.fromEntries(openUrlParams);
  }
};
