import fetchApi from "./fetchApi";

import { readDataCollect } from "../options/dataCollect";
import { API_SERVER_URL } from "../lib/constants";

export default async (logData) => {
  const dataCollect = await readDataCollect();
  if (dataCollect) {
    const logEndpoint = logData.level === "info" ? "/log/access" : "/log/error";
    const url = new URL(`${API_SERVER_URL}${logEndpoint}`);
    fetchApi({
      url,
      reqInit: {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          timestamp: new Date().toISOString(),
          ...logData,
        }),
      },
    });
  }
};
