import getSources from "../background/sources/getSources";
import { getInstitutions } from "./institutionList";
import { getLocalStorageKeyData } from "../lib/storage";
import { hasGScholarSource } from "./gScholarLib";

import { currentBrowser } from "../lib/constants";

const SOURCES_STORAGE_KEY = "sources";

export const getInstSourceItems = () =>
  Array.from(document.querySelectorAll("#sourceList .list-group-item"));

export const getInstSources = () =>
  getInstSourceItems().map((instSource) => ({
    id: instSource?.dataset?.sourceId,
    name: instSource?.textContent,
  })); // Get selected institutions' source list

export const updateInstSources = (sources = []) => {
  const { unpaywallSource, istexSource } = getSources(); // Get default sources
  const addedInstSources =
    sources.length === 0 && getInstitutions().length === 0
      ? [
          { id: unpaywallSource.id, name: unpaywallSource.name },
          { id: istexSource.id, name: istexSource.name },
        ]
      : sources;
  const sourceList = addedInstSources.map(({ id, name }) => {
    const sourceEl = document.createElement("div");
    sourceEl.classList.add("list-group-item");
    sourceEl.textContent = name;
    sourceEl.dataset.sourceId = id;
    sourceEl.insertAdjacentHTML(
      "afterbegin",
      `<i class="bi bi-grip-vertical fs-5 me-3"></i>`
    );
    return sourceEl;
  });
  document.getElementById("sourceList").replaceChildren(...sourceList);
  document.getElementById("source-container").hidden = !(
    getInstitutions().length > 0
  );
  document.getElementById("gs-container").hidden = !(
    getInstitutions().length > 0 && hasGScholarSource()
  );
};

export const saveInstSources = async () => {
  await currentBrowser.storage.local.set({
    [SOURCES_STORAGE_KEY]: getInstSources(),
  });
};

export const readInstSources = async () => {
  const { unpaywallSource, istexSource } = getSources();
  const {
    [SOURCES_STORAGE_KEY]: savedSources = [
      { id: unpaywallSource.id, name: unpaywallSource.name },
      { id: istexSource.id, name: istexSource.name },
    ],
  } = await getLocalStorageKeyData(SOURCES_STORAGE_KEY);
  return savedSources;
};
