import { UAParser } from "ua-parser-js";

import { currentBrowser } from "../lib/constants";

const LEVEL_ERROR = "error";

export default async ({ url, reqInit = {} }) => {
  let response;
  try {
    const response = await fetch(url, {
      signal: AbortSignal.timeout(10000), // Abort request after 10 sec. timeout
      ...reqInit,
    });
    if (!response?.ok) throw new TypeError("Bad response status");
    return { response };
  } catch (err) {
    console.error(err);
    const manifestVersion = currentBrowser.runtime.getManifest().version;
    const userAgentData = new UAParser().getResult();
    const error = {
      level: LEVEL_ERROR,
      manifestVersion,
      userAgentData,
      message: err.message,
      status: response?.status,
      statusText: response?.statusText,
    };
    return { error };
  }
};
