import { urlBase64ToUint8Array } from "../../lib/tools";
import fetchApi from "../../lib/fetchApi";

import { currentBrowser, API_SERVER_URL } from "../../lib/constants";

const VAPID_PUBLIC_KEY = process.env.VAPID_PUBLIC_KEY;
export const registerServiceWorker = async () => {
  try {
    await navigator?.serviceWorker.register("./serviceWorker.js"); // Register a Service Worker
  } catch (error) {
    console.error(`Service Worker registration failed: ${error}`);
  }
};

export const subscribeToWebPush = async () => {
  await navigator?.serviceWorker?.ready
    .then(async (registration) => {
      const subscription = await registration?.pushManager.subscribe({
        userVisibleOnly: true,
        applicationServerKey: urlBase64ToUint8Array(VAPID_PUBLIC_KEY),
      });
      if (subscription) {
        const subscriptionJson = subscription.toJSON();
        currentBrowser.runtime.setUninstallURL(
          `${API_SERVER_URL}/subscription/remove/${subscriptionJson.keys.auth}`
        ); // Set uninstall URL to remove notification subscription on addon uninstall
        await fetchApi({
          url: `${API_SERVER_URL}/subscription`,
          reqInit: {
            body: JSON.stringify(subscription.toJSON()),
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
          },
        });
      }
    })
    .catch((error) => console.error(error));
};

export const getPushSubscription = async () => {
  return navigator?.serviceWorker.ready.then((registration) =>
    registration.pushManager.getSubscription()
  );
};

export const getSWRegistration = async () =>
  await navigator?.serviceWorker.getRegistration();
