import EbscoApi from "../api/EbscoApi";

export default class {
  constructor({ name, ftfAccount, ignoredLinkNames, gScholar, sourceId }) {
    this.name = name;
    this.id = sourceId;
    this.ftfAccount = ftfAccount;
    this.ignoredLinkNames = ignoredLinkNames;
    if (gScholar) this.gScholar = gScholar;
    this.resolverApi = new EbscoApi(
      this.name,
      this.ftfAccount,
      this.ignoredLinkNames
    );
  }
}
