import fetchApi from "../../lib/fetchApi";
import sendLogsToServer from "../../lib/sendLogsToServer";
import { buildGetOpenUrlParams, buildPostOpenUrlParams } from "./openUrl";
import { getCacheData, setCacheData } from "../../lib/storage";

import { SID_CLICKANDREAD, MULTI_TYPE } from "../../lib/constants";

export default class {
  constructor(sourceName) {
    this.apiOpenUrl = "https://api.istex.fr/document/openurl";
    this.apiMultiOpenUrl = new URL(
      "https://api.istex.fr/document/multiopenurl"
    );
    this.sourceName = sourceName;
    this.cacheMaxAge = 86400000 * 30; // 86400000 milliseconds = 1 day * 30 days
  }

  /**
   * Resolve identifiers on Istex API
   * @param  {array} idList Array of resource identifiers {id, type}
   * @returns {array} Resolved Id List {id, url, source}
   */
  async resolveIds({ idList = [] }) {
    let apiOpenUrl;
    const cachedIds = [];
    const searchedIds = [];
    let resolvedIds = [];
    if (!idList || idList.length === 0) return resolvedIds;
    // Resolve IDs on cache storage
    for (const identifier of idList) {
      apiOpenUrl = new URL(this.apiOpenUrl);
      apiOpenUrl.search = "";
      apiOpenUrl.searchParams.set("sid", `${SID_CLICKANDREAD}`);
      buildGetOpenUrlParams(identifier).forEach((val, key) =>
        apiOpenUrl.searchParams.set(key, val)
      );
      const cacheData = await getCacheData({
        url: apiOpenUrl,
        maxAge: this.cacheMaxAge,
      });
      const cacheJson = await cacheData?.json();
      if (cacheJson)
        cachedIds.push({
          id: identifier.id,
          url: cacheJson?.resourceUrl,
          source: this.sourceName,
        });
      else searchedIds.push(identifier);
    }
    // Resolve searched IDs on API
    if (searchedIds.length > 0) {
      const openUrlParamList = searchedIds.map((identifier) =>
        buildPostOpenUrlParams(identifier)
      );
      const reqInit = {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(openUrlParamList),
      };
      this.apiMultiOpenUrl.searchParams.set("sid", `${SID_CLICKANDREAD}`);
      this.apiMultiOpenUrl.searchParams.set(
        "bodySize",
        `${openUrlParamList.length}`
      );
      const { response, error } = await fetchApi({
        url: this.apiMultiOpenUrl,
        reqInit,
      });
      if (response?.ok) {
        try {
          const apiData = await response.json();
          if (apiData?.length > 0) {
            if (searchedIds.length !== apiData?.length)
              throw new RangeError("API response data length error", {
                cause: `searchedIds(${searchedIds.length}) !== apiData(${apiData?.length})`,
              });
            for (const [index, resolvedId] of apiData.entries()) {
              // Update cache with API response data
              apiOpenUrl = new URL(this.apiOpenUrl);
              apiOpenUrl.search = "";
              apiOpenUrl.searchParams.set("sid", `${SID_CLICKANDREAD}`);
              buildGetOpenUrlParams(searchedIds[index]).forEach((val, key) =>
                apiOpenUrl.searchParams.set(key, val)
              );
              const responseData =
                resolvedId?.resourceUrl || resolvedId?.error?.code < 500
                  ? resolvedId
                  : null;
              if (responseData) {
                await setCacheData({
                  url: apiOpenUrl.href,
                  response: new Response(JSON.stringify(responseData), {
                    status: response?.statusCode,
                    statusText: response?.statusText,
                    headers: response?.headers,
                  }),
                });
                resolvedIds.push({
                  id: searchedIds[index]?.id,
                  type: searchedIds[index]?.type,
                  url: resolvedId?.resourceUrl,
                  source: this.sourceName,
                });
              }
            }
          }
        } catch (error) {
          console.error(error);
        }
      } else if (error && error.status !== 404)
        sendLogsToServer({
          source: this.sourceName,
          type: MULTI_TYPE,
          ...error,
          message: error?.message?._message
            ? error.message._message
            : error?.message,
        });
    }
    resolvedIds = resolvedIds
      .concat(cachedIds)
      .filter((resolvedId) => resolvedId?.url);
    return resolvedIds;
  }
}
