import fetchApi from "../../lib/fetchApi";
import sendLogsToServer from "../../lib/sendLogsToServer";
import { getCacheData, setCacheData } from "../../lib/storage";

import { SID_CLICKANDREAD, DOI_TYPE } from "../../lib/constants";

export default class {
  constructor(sourceName) {
    this.apiUrl = new URL("https://unpaywall.inist.fr/api/graphql");
    this.apiKey = "lQgiLLjcEaN71Pq81rl0";
    this.sourceName = sourceName;
    this.cacheMaxAge = 86400000 * 7; // 86400000 milliseconds = 1 day * 7 days
  }

  /**
   * Resolve identifiers on EzUnpaywall API
   * @param  {array} idList Array of resource identifiers {id, type}
   * @returns {array} Resolved Id List {id, url, source}
   */
  async resolveIds({ idList = [] }) {
    const cachedIds = [];
    const searchedIds = [];
    let resolvedIds = [];
    if (!idList || idList.length === 0) return resolvedIds;
    // Filter IDs on DOI type
    const doiIdList = idList.filter(
      (identifier) => identifier.type === DOI_TYPE
    );
    if (!doiIdList || doiIdList.length === 0) return resolvedIds;

    // Resolve IDs on cache storage
    this.apiUrl.searchParams.set("sid", `${SID_CLICKANDREAD}`);
    for (const doiId of doiIdList) {
      this.apiUrl.searchParams.set(
        "query",
        `{ GetByDOI(dois: ["(${doiId?.id})"]){doi, is_oa, best_oa_location{url_for_landing_page, url_for_pdf}} }`
      );
      const cacheData = await getCacheData({
        url: this.apiUrl,
        maxAge: this.cacheMaxAge,
      });
      if (cacheData) {
        const cacheJson = await cacheData?.json();
        if (cacheJson?.data?.GetByDOI)
          cachedIds.push(cacheJson.data?.GetByDOI[0]);
      } else searchedIds.push(doiId.id);
    }
    // Resolve searched IDs on API
    if (searchedIds.length > 0) {
      const reqInit = {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "x-api-key": this.apiKey,
        },
        body: JSON.stringify({
          query:
            "query ($dois: [ID!]!){ GetByDOI(dois: $dois){doi, is_oa, best_oa_location{url_for_landing_page, url_for_pdf}} }",
          variables: {
            dois: searchedIds,
          },
        }),
      };
      this.apiUrl.searchParams.delete("query");
      const { response, error } = await fetchApi({ url: this.apiUrl, reqInit });
      if (response?.ok) {
        const apiData = await response.json();
        if (apiData?.data?.GetByDOI?.length > 0)
          resolvedIds = apiData.data.GetByDOI;
      } else if (error && error?.status !== 404)
        sendLogsToServer({
          source: this.sourceName,
          type: DOI_TYPE,
          ...error,
        });
      // Update cache with API response data
      for (const searchedId of searchedIds) {
        const resolvedId = resolvedIds.find(
          (resolvedId) =>
            resolvedId.doi.toLowerCase() === searchedId.toLowerCase()
        );
        this.apiUrl.searchParams.set(
          "query",
          `{ GetByDOI(dois: ["(${searchedId})"]){doi, is_oa, best_oa_location{url_for_landing_page, url_for_pdf}} }`
        );
        await setCacheData({
          url: this.apiUrl,
          response: new Response(
            JSON.stringify({
              data: { GetByDOI: resolvedId ? [resolvedId] : [] },
            }),
            {
              status: response?.statusCode,
              statusText: response?.statusText,
              headers: response?.headers,
            }
          ),
        });
      }
    }
    resolvedIds = resolvedIds
      .concat(cachedIds)
      .filter((data) => data?.is_oa && data?.best_oa_location)
      .map((data) => ({
        id: data?.doi,
        url:
          data?.best_oa_location?.url_for_pdf ||
          data?.best_oa_location?.url_for_landing_page,
        source: this.sourceName,
      }));
    return resolvedIds;
  }
}
