export const initDataCollectModal = async () => {
  const dataCollect = await readDataCollect();
  if (dataCollect === undefined) {
    buildDataCollectModal(); // Open dialog for anonymous data collection consent
    validateDataCollect();
  }
};

const buildDataCollectModal = () => {
  const { pp, pl, dl } = currentBrowser.i18n.getUILanguage().startsWith("fr")
    ? dataCollectModalBodyInfo.fr
    : dataCollectModalBodyInfo.en;
  const infoList = document.createElement("div");
  for (const p of pl) {
    const ulElement = document.createElement("p");
    if (p.endsWith("[PP]")) {
      const ppLink = document.createElement("a");
      ppLink.target = "_blank";
      ppLink.rel = "noopener noreferrer";
      ppLink.href = pp.href;
      ppLink.textContent = pp.text;
      ulElement.append(p.replace("[PP]", ""));
      ulElement.append(ppLink);
    } else ulElement.append(p);
    infoList.appendChild(ulElement);
  }
  const dataList = document.createElement("ul");
  for (const d of dl) {
    const ulElement = document.createElement("li");
    ulElement.append(d);
    dataList.appendChild(ulElement);
  }
  const modalBodyElem = document.createElement("div");
  modalBodyElem.append(infoList);
  modalBodyElem.append(dataList);
  $(".dataCollectModalBody").append(modalBodyElem);
  const dataCollectModal = new Modal(
    document.getElementById("dataCollectModal")
  ); // Data collection consent modal
  dataCollectModal.show();
};

const validateDataCollect = async () => {
  $("#dataCollectModalAgreeBtn").on("click", () => saveDataCollect(true));
  $("#dataCollectModalDeclineBtn").on("click", () => saveDataCollect(false));
};

export const readDataCollect = async () => {
  const { [DATA_COLLECT_STORAGE_KEY]: dataCollect } =
    await getLocalStorageKeyData(DATA_COLLECT_STORAGE_KEY);
  return dataCollect;
};

const saveDataCollect = async (dataCollectChoice) => {
  await currentBrowser.storage.local.set({
    [DATA_COLLECT_STORAGE_KEY]: dataCollectChoice,
  });
};
