import UnpaywallSource from "./UnpaywallSource";
import IstexSource from "./IstexSource";
import PanistSource from "./PanistSource";
import EbscoSource from "./EbscoSource";
import ExLibrisAlmaSource from "./ExLibrisAlmaSource";
import ExLibrisSfxSource from "./ExLibrisSfxSource";
import StEtienneUnivSource from "./StEtienneUnivSource";
import sources from "../../data/sources.json" assert { type: "json" };

import { BIBCNRS_SOURCE_PREFIX } from "../../lib/constants";

/**
 * Source list instantiating
 * @returns {object} source instances object
 */
export default () => {
  let unpaywallSource;
  let istexSource;
  let panistSource;
  const bibSources = [];
  const sourceList = [];
  sources
    ?.map((source) => {
      const currentSource = { ...source };
      if (source.sourceId.startsWith(BIBCNRS_SOURCE_PREFIX)) {
        currentSource.name = navigator.language.startsWith("fr")
          ? source.name.fr
          : source.name.en;
      } // Format the BibCnrs sources' name along with navigator language
      currentSource.name = currentSource.name.toLocaleUpperCase(); // Uppercase source name
      return currentSource;
    })
    .map((source) => {
      if (source?.resolver === "unpaywall") {
        unpaywallSource = new UnpaywallSource(source); // Instantiate default Unpaywall source
        sourceList.push(unpaywallSource);
      }
      if (source?.resolver === "istex") {
        istexSource = new IstexSource(source); // Instantiate default Istex source
        sourceList.push(istexSource);
      }
      if (source?.resolver === "panist") {
        panistSource = new PanistSource(source);
        sourceList.push(panistSource); // Instantiate Panist source
      }
      if (
        source?.resolver === "ebsco" &&
        ((source?.dev && DEV) || !source?.dev)
      ) {
        if (source.sourceId.startsWith(BIBCNRS_SOURCE_PREFIX)) {
          bibSources.push(new EbscoSource(source));
        }
        sourceList.push(new EbscoSource(source)); // Instantiate EBSCO sources
      }
      if (
        source?.resolver === "exlibrisAlma" &&
        ((source?.dev && DEV) || !source?.dev)
      )
        sourceList.push(new ExLibrisAlmaSource(source)); // Instantiate EXLIBRIS-ALMA sources
      if (
        source?.resolver === "exlibrisSfx" &&
        ((source?.dev && DEV) || !source?.dev)
      )
        sourceList.push(new ExLibrisSfxSource(source)); // Instantiate EXLIBRIS-SFX sources
      if (source?.resolver === "steUniv")
        sourceList.push(new StEtienneUnivSource(source)); // Instantiate Univ. Saint-Etienne source
    });
  const gScholarSourceList = sourceList?.filter((source) => source.gScholar); // Source list having a Google Scholar library
  return {
    sourceList,
    unpaywallSource,
    istexSource,
    panistSource,
    gScholarSourceList,
    bibSources,
  };
};
