import getSources from "../sources/getSources";
import { readInstSources } from "../../options/instSourceList";

export default async () => {
  const { sourceList = [] } = getSources();
  const savedSources = await readInstSources(); // Read chosen source list from local storage
  return savedSources?.map(
    (savedSource) =>
      sourceList?.find((source) => source.id === savedSource.id).resolverApi
  ); // Filter chosen sources' APIs
};
