import { mapLimit } from "async";

import fetchApi from "../../lib/fetchApi";
import sendLogsToServer from "../../lib/sendLogsToServer";
import { buildGetOpenUrlParams } from "./openUrl";
import { getCacheData, setCacheData } from "../../lib/storage";

import { SID_CLICKANDREAD } from "../../lib/constants";

export default class {
  constructor(sourceName, resolverUrl, ignoredLinkNames) {
    this.apiOpenUrl = resolverUrl;
    this.almaReqCount = 0;
    this.almaReqLimit = 200;
    this.sourceName = sourceName;
    this.ignoredLinkNames = ignoredLinkNames;
    this.cacheMaxAge = 86400000 * 1; // 86400000 milliseconds * 1 day
  }
  /**
   * Resolve identifiers on ExLibris ALMA API
   * @param  {array} idList Array of resource identifiers {id, node, type}
   * @returns {array} Resolved Id List {id, url, source}
   */
  async resolveIds({ idList = [] }) {
    let resolvedIds = [];
    if (!idList || idList.length === 0) return resolvedIds;
    resolvedIds = await mapLimit(idList, 30, async (identifier) => {
      if (this.almaReqCount <= this.almaReqLimit) {
        const apiOpenUrl = new URL(this.apiOpenUrl);
        let resolvedIdData;
        // Resolve ID on cache storage
        apiOpenUrl.search = "";
        apiOpenUrl.searchParams.set("sid", `${SID_CLICKANDREAD}`);
        apiOpenUrl.searchParams.set("svc_dat", "CTO");
        buildGetOpenUrlParams(identifier).forEach((val, key) =>
          apiOpenUrl.searchParams.set(key, val)
        );
        const cacheData = await getCacheData({
          url: apiOpenUrl,
          maxAge: this.cacheMaxAge,
        });
        resolvedIdData = await cacheData?.text();
        if (!resolvedIdData) {
          // Resolve ID on API
          this.almaReqCount++;
          const { response, error } = await fetchApi({
            url: apiOpenUrl,
          });
          if (response?.ok) {
            await setCacheData({
              url: apiOpenUrl,
              response: response.clone(),
            }); // Add API response to cache
            resolvedIdData = await response.text();
          } else if (error) {
            sendLogsToServer({
              source: this.sourceName,
              type: identifier.type,
              ...error,
            });
            return error;
          }
        }
        // Parse XML data
        if (resolvedIdData) {
          const xmlData = new window.DOMParser().parseFromString(
            resolvedIdData,
            "text/xml"
          );
          if (xmlData) {
            // if single_service or full_text_indicator is true, there is electronic service available
            const fullTextIndicator =
              xmlData?.querySelector(
                "context_object keys key[id='full_text_indicator']"
              )?.textContent === "true" ||
              xmlData?.querySelector(
                "context_object keys key[id='available_services']"
              )?.textContent === "single_service" ||
              xmlData?.querySelector(
                "context_object keys key[id='available_services']"
              )?.textContent === "viewit";
            if (fullTextIndicator) {
              // Take first service under the context_services section
              // and make sure it is not filtered, not having Filtered true.
              const isFiltered =
                xmlData
                  ?.querySelector("context_services")
                  ?.firstElementChild?.querySelector("keys key[id='Filtered']")
                  ?.textContent === "true";
              // take the resolution_url value and redirect to this url
              const resolutionUrl = xmlData
                ?.querySelector("context_services")
                ?.firstElementChild?.querySelector(
                  "resolution_url"
                )?.textContent;
              if (!isFiltered && resolutionUrl)
                return {
                  id: identifier.id,
                  url: resolutionUrl,
                  source: this.sourceName,
                };
            }
          }
        }
      }
    });
    resolvedIds = resolvedIds.filter(
      (identifier) => identifier?.id && identifier?.url
    );
    return resolvedIds;
  }
}
